<template>
  <div data-index="index">
    <ul>
      <li :class="{editing: editing}">
          <p>{{ student.idStudent }}</p>
          <input type="text" v-model="student.idStudent">
        </li>
      <li :class="{editing: editing}">
          <p>{{ student.fullName }}</p>
          <input type="text" v-model="student.fullName">
        </li>
      <li :class="{editing: editing}">
          <p>{{ student.gender }}</p>
          <label for="">M</label>
          <input type="radio" v-model="student.gender" value="Male">
          <label for="">F</label>
          <input type="radio" v-model="student.gender" value="Female">
        </li>
      <li :class="{editing: editing}">
          <p>{{ student.department }}</p>
          <input type="text" v-model="student.department">
        </li>
      <li v-if="!editing" @click="handleEdit()">Sửa</li>
      <li v-else @click="handleSave()">Lưu</li>
      <li @click="handleDelete(index)">Xóa</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
      return {
          editing: false,
      }
  },
  props: ["student", "index","list"],
  methods: {
      handleEdit() {
          this.editing = !this.editing
      },
      handleSave() {
          this.editing = !this.editing
          localStorage.setItem('quanlisinhvien', JSON.stringify(this.list));
      },
      handleDelete(index) {
        this.list.splice(index, 1)
        localStorage.setItem('quanlisinhvien', JSON.stringify(this.list));
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
li {
  display: inline-block;
  background-color: rgb(236, 236, 236);
  border: 1px solid #dad8d8;
  padding: 5px 10px;
  color: #000000;
  width: 150px;
}
li:nth-child(5) {
  width: 70px;
  background-color: rgb(48, 154, 216);
  color: #fff;
}
li:nth-child(6) {
  width: 70px;
  background-color: rgb(248, 26, 26);
  color: #fff;
}
li input {
    display: none;
}
li label {
    display: none;
}
li.editing input {
    display: inline-block;
    max-width: 95%;
}
li.editing label {
    display: inline-block;
}
li.editing p {
    display: none;
}
</style>
