<template>
  <div id="infoAdding" :class="{adding: adding, close: close}">
    <div class="groupInput">
      <label for="idStudent">ID Student</label>
      <input type="text" id="idStudent"  v-model="idStudent">
    </div>
    <div class="groupInput">
      <label for="FullName">Full Name</label>
      <input type="text" id="FullName" v-model="fullName">
    </div>
    <div class="groupInput">
      <label>Gender</label>
      <input type="radio" value="Male" v-model="gender"> Male
      <input type="radio" value="Female" v-model="gender"> Female
    </div>
    <div class="groupInput">
      <label for="Department">Department</label>
      <input type="text" id="Department"  v-model="department">
    </div>
    <div class="save">
      <button  @click="handeleList({idStudent,fullName,gender,department})">Save</button>
    </div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      close: false,
    }
  },
  methods: {
    handeleList(info) {
      this.list.push(info)
      localStorage.setItem('quanlisinhvien', JSON.stringify(this.list))
      // this.list.push(info)
      this.close = true
    }
  },
  props: ['adding', 'list'],
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #infoAdding {
        background-color: rgb(235, 235, 235);
        max-width: 800px;
        display: flex;
        flex-wrap: wrap;
        display: none;
        padding: 10px 15px;
    }
    #infoAdding.adding {
        display: block;
    }
    #infoAdding.close {
        display: none;
    }
    .groupInput {
      width: 400px;
      margin-bottom: 10px;
    }
    .groupInput label {
      display: block;
      margin: 5px 0px;
      color: rgb(65, 64, 64);
    }
    .groupInput input {
      border: none;
      border-radius: 3px;
    }
    .save button {
      background-color: rgb(69, 69, 248);
      color: aliceblue;
      border: none;
      border-radius: 3px;
      padding: 5px 10px;
    }
</style>
